<template>
  <div class="main">
    <div class="container">
      <div class="card" @click="cardClick()">
        <i class="iconfont icon-data card-icon"></i>
        <div class="card-txt">数据分析</div>
      </div>
      <div class="card" @click="handleCardClick('FileMake')">
        <i class="iconfont icon-file card-icon"></i>
        <div class="card-txt">文件追踪</div>
      </div>
    </div>
    <div class="header clearfix">
      <div class="logo-txt fl">文件管理系统</div>
    </div>
    <div class="footer">
      <p>网域复兴科技有限公司 All Rights Reserved</p>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
      };
    },
    methods: {
      // 跳轉到其他系統
      handleCardClick(name) {
        this.$router.push({name: name});
      },
      cardClick() {
        window.location.href = 'http://localhost:8080';
      }
    },
    mounted() {
      
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .main {
    height: 100%;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
    color: #fff;
    background-color: #0e85af;
  }
  .header {
    width: calc(100% - 80px);
    height: 50px;
    line-height: 50px;
    background-color: #292d30;
    padding: 0 40px;
    position: absolute;
    top: 0;
    left: 0;
  }
  .logo {
    height: 35px;
    margin-top: 7px;
  }
  .logo-txt {
    margin-left: 15px;
    font-size: 18px;
  }
  .right-set {
    background-color: #292d30;
    border: none;
    color: #fff;
  }
  .footer {
    width: 100%;
    height: 50px;
    align-items: center;
    display: -webkit-flex;
    color: #556e84;
    background-color: #292d30;
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
  }
  .footer p {
    display: block;
    width: 1000px;
    margin: 0 auto;
    color: #fff;
  }
  .container {
    width: 960px;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    justify-content: center;
    position: absolute;
    top: 100px;
    left: calc(50% - 480px);
  }
  .card {
    width: 200px;
    background-color: rgba(255, 255, 255, .5);
    color: #363d44;
    text-align: center;
    padding: 20px 0;
    cursor: pointer;
    margin: 20px;
  }
  .card:hover {
    transform: scale(1.15);
  }
  .card-icon {
    display: block;
    font-size: 60px;
  }
  .card-txt {
    margin-top: 10px;
    font-size: 20px;
  }
  .hide {
    display: none;
  }
</style>



