// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from 'element-ui'
import '../theme/index.css'
import axios from './http'
import router from './router'
import './assets/css/iconfont.css'
import App from './App'

Vue.config.productionTip = false

Vue.use(ElementUI)

//网站根目录
Vue.prototype.$rootPath = "http://192.168.1.42:8081";

//js获取网站根路径(站点及虚拟目录)
Vue.prototype.$RootPath = function RootPath() {
  var strFullPath = window.document.location.href;
  var strPath = window.document.location.pathname;
  var pos = strFullPath.indexOf(strPath);
  var prePath = strFullPath.substring(0, pos);
  // var postPath = strPath.substring(0, strPath.substr(1).indexOf('/') + 1);
  //return (prePath + postPath);如果发布IIS，有虚假目录用用这句
  return (prePath);
}
//获取当前系统时间
Vue.prototype.$getNowFormatDate = function getNowFormatDate() {
  var date = new Date();
  var seperator1 = "-";
  var seperator2 = ":";
  var month = date.getMonth() + 1;
  var strDate = date.getDate();
  if (month >= 1 && month <= 9) {
    month = "0" + month;
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = "0" + strDate;
  }
  var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate +
    " " + date.getHours() + seperator2 + date.getMinutes() +
    seperator2 + date.getSeconds();
  return currentdate;
}
/** 
 * 格式化日期为字符串表示 
 * @param datetime:Date 要格式化的日期对象 
 * @param format:String 日期格式 
 */
Vue.prototype.$formatDate = function (datetime, format) {
  var cfg = {
      MMM: ['一', '二', '三', '四', '五', '六', '七', '八', '九', '十', '十一', '十二'],
      MMMM: ['一', '二', '三', '四', '五', '六', '七', '八', '九', '十', '十一', '十二']
    },
    values = {
      y: datetime.getFullYear(),
      M: datetime.getMonth() + 1,
      d: datetime.getDate(),
      H: datetime.getHours(),
      m: datetime.getMinutes(),
      s: datetime.getSeconds(),
      S: datetime.getMilliseconds()
    };
  /*用正则表达式拆分日期格式各个元素*/
  var elems = format.match(/y+|M+|d+|H+|m+|s+|S+|[^yMdHmsS]/g);
  //将日期元素替换为实际的值 
  for (var i = 0; i < elems.length; i++) {
    if (cfg[elems[i]]) {
      elems[i] = cfg[elems[i]][values[elems[i].charAt(0)]];
    } else if (values[elems[i].charAt(0)]) {
      elems[i] = formatNumber(values[elems[i].charAt(0)], elems[i].replace(/./g, '0'));
    }
  }
  return elems.join('');
}
/** 
 * 格式化整数 
 * @param number:number 要格式化的整数 
 * @param fmt:string 整数格式 
 */
function formatNumber(number, fmt) {
  number = number + '';
  if (fmt.length > number.length) {
    return fmt.substring(number.length) + number;
  }
  return number;
}
//封装axios
Vue.prototype.$http = axios

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: {
    App
  }
})



