<template>
  <div class="main">
    <div class="inner">
      <el-row class="fullHeight">
        <el-col :span="12" class="fullHeight">
          <el-upload class="upload" ref="upload" :action="this.$rootPath + '/fms/file/upload'" :on-success="uploadSuccess" :on-remove="handleRemove" :file-list="fileList" multiple>
            <el-button slot="trigger" size="small">上传需标记文件</el-button>
            <div class="upload-title">上传文件列表</div>
          </el-upload>
          <div class="fileMake">
            <div class="upload-title">标记文件信息<span slot="tip" class="el-upload__tip">（对以上文件进行统一标记）</span></div>
            <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="makeform">
              <el-form-item label="备注" prop="remark">
                <el-input v-model="ruleForm.remark" class="form-input"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')" :disabled="subBtnDis">开始标记</el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-col>
        <el-col :span="12" class="fullHeight">
          <div class="upload-title">本次完成标记文件
            <el-button size="small" @click="handleDownFiles">下载已标记文件</el-button>
          </div>
          <el-table :data="tableData1" @select="selectRow" @select-all="selectRow" stripe style="width: 100%;" height="100%" class="table">
            <el-table-column type="selection" width="50">
            </el-table-column>
            <el-table-column prop="name" label="文件名称">
            </el-table-column>
            <el-table-column label="状态" width="150">
              <template scope="scope">
                        <el-tag :type="scope.row.status">{{scope.row.mes}}</el-tag>
</template>
            </el-table-column>
          </el-table>
          <el-table :data="tableData2" stripe style="width: 100%;border-top: none;margin-top: 0" height="100%" class="table" :show-header="false">
            <el-table-column width="50">
            </el-table-column>
            <el-table-column prop="name" label="文件名称">
            </el-table-column>
            <el-table-column label="状态" width="150">
<template scope="scope">
  <el-tag :type="scope.row.status">
    {{scope.row.mes}}</el-tag>
</template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        fileList: [],
        ruleForm: {
          remark: ''
        },
        tableData1: [],
        tableData2: [],
        subBtnDis: true, // 开始标记按钮状态
        selection: [] //记录当前选中项
      };
    },
    methods: {
      // 删除已上传文件
      handleRemove(file, fileList) {
        if (fileList.length <= 0) {
          this.subBtnDis = true;
        }
        this.fileList = fileList;
      },
      // 文件上传成功
      uploadSuccess(response, file, fileList) {
        if (fileList.length > 0) {
          this.subBtnDis = false;
        }
        this.fileList = fileList;
        this.tableData1 = [];
        this.tableData2 = [];
      },
      // 开始标记
      submitForm() {
        const vm = this;
        let fileList = vm.fileList;
        if (fileList.length <= '0') {
          this.$message({
            message: '请先选择要标记的文件',
            type: 'warning'
          });
        } else {
          let arr = new Array();
          for (let i in vm.fileList) {
            arr.push(vm.fileList[i].response.data.id);
          }
          arr = JSON.stringify(arr);
          this.$http.get('/fms/file/signFile', {
              params: {
                fileIds: arr,
                tag: vm.ruleForm.tag,
                remark: vm.ruleForm.remark,
                fireWallNumber: vm.ruleForm.code
              }
            })
            .then(function(response) {
              let data = response.data;
              for (let i in data) {
                let arr = new Array();
                arr.name = data[i].fileName;
                arr.id = data[i].id;
                if (data[i].status == 1) { // 成功
                  arr.status = 'success';
                  arr.mes = '成功';
                  vm.tableData1.push(arr);
                } else if (data[i].status == 3) { // 格式错误
                  arr.status = 'danger';
                  arr.mes = '格式错误';
                  vm.tableData2.push(arr);
                } else if (data[i].status == 2) { // 失败
                  arr.status = 'danger';
                  arr.mes = '失败';
                  vm.tableData2.push(arr);
                }
              }
              vm.$refs.upload.clearFiles();
              vm.subBtnDis = true;
            })
        }
      },
      // 表格行选择
      selectRow(selection) {
        let arr = new Array();
        for (let i in selection) {
          arr.push(selection[i].id);
        }
        this.selection = JSON.stringify(arr);
        console.log(selection);
      },
      // 下载文件
      handleDownFiles() {
        const vm = this;
        console.log(this.selection);
        if (this.selection.length <= '0') {
          this.$message({
            message: vm.language.downFilesMes,
            type: 'warning'
          });
        } else {
          window.open(vm.$rootPath + '/fms/file/downLoadFile?fileIds=' + vm.selection);
        }
      },
      rowStyle() {
        return 'row-style'
      }
    },
    mounted() {
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .main {
    height: calc(100% - 40px);
    position: relative;
    padding: 20px;
    background-color: #e4e7ea;
  }
  .upload {
    width: 100%;
    max-width: 500px;
    height: calc(100% - 170px);
  }
  .upload-title {
    margin-top: 10px;
  }
  .el-upload__tip {
    margin-left: 10px;
  }
  .fileMake {
    height: auto;
  }
  .makeform {
    margin-top: 10px;
  }
  .form-input {
    max-width: 300px;
  }
  .table {
    margin-top: 10px;
    height: calc((100% - 48px)/2);
  }
</style>

<style>
  .el-upload-list {
    height: calc(100% - 60px);
    overflow: auto;
    border-bottom: 1px solid rgb(223, 231, 236);
  }
  .row-style {
    display: none;
  }
</style>




