import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/pages/Main'
import error from '@/pages/Error'
import FileMake from '@/pages/FileMake'

Vue.use(Router)

const router = new Router({
  // html5history模式，返回真实路径需要后台配置
  // mode: 'history',
  routes: [{
      path: '/main',
      name: 'Main',
      component: Main
    },
    {
      path: '/error',
      name: 'Error',
      component: error
    },
    {
      path: '/file-make',
      name: 'FileMake',
      component: FileMake
    }
  ]
})

export default router



